<template>
  <div>
    <el-main style=" border:8px dotted gray;  background-color: #f5f6f7;height:660px">
      <el-form :label-position="labelPosition" :model="form" label-width="80px">
        <el-form-item label=":">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 500}"
            v-model="form.question"
            style="width:600px"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
        <el-form-item label=":">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 500}"
            v-model="form.nlp"
            style="width:600px"
          ></el-input>
        </el-form-item>
        <el-form-item label="java">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 500}"
            v-model="form.java"
            style="width:600px"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 500}"
            v-model="form.precess"
            style="width:600px"
          ></el-input>
        </el-form-item>
      </el-form>
    </el-main>
  </div>
</template>

<script>
import Diagram from "./components/Diagram.vue";
import { reasoning } from "@/api/reasoning";
import { Loading } from "element-ui";

export default {
  data() {
    return {
      labelPosition: "top",
      form: {
        question: "",
        nlp: "",
        java: "",
        precess: ""
      }
    };
  },
  methods: {
    onSubmit() {
      let loadingInstance = Loading.service({
        fullscreen: true,
        text: ""
      });

      reasoning(this.form.question).then(response => {
        if (response.code == 200) {
          this.$notify({
            title: "",
            message: response.msg,
            type: "success"
          });
          this.form.nlp = response.data.nlp;
          this.form.java = response.data.java;
          this.form.precess = response.data.precess;
          this.$nextTick(() => {
            //  Loading 
            loadingInstance.close();
          });
        }
      });
    }
  },
  name: "secondpage",
  components: {
    Diagram
  }
};
</script>

